import React, { useState, useEffect, useRef } from "react";
import ReactDOM from "react-dom";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation,
} from "react-router-dom";
import { Helmet } from "react-helmet";
import { Element } from "react-scroll";
import {
  createMuiTheme,
  MuiThemeProvider,
  withStyles,
} from "@material-ui/core/styles";
import Tooltip from "@material-ui/core/Tooltip";
import Grid from "@material-ui/core/Grid";

import Navbar from "./components/Navbar";
import InProgressPage from "./pages/InProgressPage";
import LandingPage from "./pages/LandingPage";
import AboutPage from "./pages/AboutPage";
import ProjectPage from "./pages/ProjectPage";
import Background from "./components/Background";
import Avatar from "@material-ui/core/Avatar";
import Footer from "./components/Footer";
import Seperation from "./components/Seperation";
import "./css/global.css";
import GA from "./utils/GoogleAnalytics";

const defaultTheme = createMuiTheme();
const theme = createMuiTheme({
  overrides: {
    MuiTooltip: {
      tooltip: {
        fontSize: "1.2em",
        color: "#3b444b",
        padding: 20,
        fontFamily: "TT Commons Bold",
        backgroundColor: "white",
        borderRadius: 10,
      },
    },
  },
});

function App() {
  const [resized, setResized] = useState(false);
  const [totalHeight, setTotalHeight] = useState(null);
  const [landingHeight, setLandingHeight] = useState(null);
  const [aboutHeight, setAboutHeight] = useState(null);
  const [projectsHeight, setProjectsHeight] = useState(null);
  const [headerHeight, setHeaderHeight] = useState(null);
  const [footerHeight, setFooterHeight] = useState(null);

  const landing = useRef(null);
  const about = useRef(null);
  const projects = useRef(null);
  const header = useRef(null);
  const footer = useRef(null);

  return (
    <Router>
      {GA.init() && <GA.RouteTracker />}
      <MuiThemeProvider theme={defaultTheme}>
        <Helmet>
          <title>Adrienne Rio</title>
          <style>{"body { background-color: #f0f5f9; }"}</style>
        </Helmet>
        <Background
          resized={resized}
          totalHeight={totalHeight}
          setResized={setResized}
        />

        <Navbar ref={header} />

        <Element name="Home">
          <LandingPage ref={landing} />
        </Element>

        {/* <Seperation header={true} /> */}

        <MuiThemeProvider theme={theme}>
          <Element name="About">
            <AboutPage name="About" ref={about} />
          </Element>
        </MuiThemeProvider>

        <Element name="Projects">
          <ProjectPage setResized={setResized} ref={projects} />
        </Element>

        <Footer ref={footer} />
      </MuiThemeProvider>
    </Router>
  );
}

export default App;
